.search_container {
    position: absolute;
    top: 26%;
    left: 50%;
    transform: translateX(-50%);
    background: white;
    width: 840px;
    padding: 10px;
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
    border-radius: 5px;
    z-index: 1000;
    align-content: center;
}

.search_container input{
    width: 98%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    outline: none;
    font-size: 14px;
    height: 26px;
}

.search_dropdown{
   margin-top: 5px;
    list-style: none;
    padding: 0;
    border: 1px solid #ddd;
    border-radius: 4px;
    overflow: hidden;
    max-height: 200px;
    overflow-y: auto;
}

.search_container ul {
    display: flex;
    justify-content: left;
    flex-direction: column;
    gap: 15px;
    overflow-x: auto;
}

.search_item {
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    transition: background 0.2s;
}

.search_item:hover {
    background: #f5f5f5;
}

.search_img {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 5px;
    margin-inside: 10px;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 130vw;
    height: 130vh;
    background: rgba(0,0,0,0.5);
    z-index: 998;
}

.no_scroll {
    overflow: hidden !important;
}

@media (min-width: 577px) and (max-width: 992px) {
    .search_container {
        width: 100%;
        top: calc(100% - 68px);
        align-content: center;
    }
    .search_container input {
        width: 80%;
    }
    .search_dropdown{
        margin-top: 5px;
        list-style: none;
        padding: 0;
        border: 1px solid #ddd;
        border-radius: 4px;
        overflow: hidden;
        max-height: 200px;
        overflow-y: auto;
    }

    .search_container ul {
        display: flex;
        justify-content: left;
        flex-direction: column;
        gap: 15px;
        overflow-x: auto;
        transform: none;
        position: unset;
        padding: 0 15px;
        background-color: white;
    }

    .search_item {
        display: flex;
        align-items: center;
        padding: 10px;
        cursor: pointer;
        transition: background 0.2s;
    }

    .search_item:hover {
        background: #f5f5f5;
    }
}

@media (min-width: 320px) and (max-width: 576px) {
    .search_container {
        width: 100%;
        top: calc(100% - 68px);
        align-content: center;
    }

    .search_container input {
        width: 80%;
    }
    .search_dropdown{
        margin-top: 5px;
        list-style: none;
        padding: 0;
        border: 1px solid #ddd;
        border-radius: 4px;
        overflow: hidden;
        max-height: 200px;
        overflow-y: auto;
    }

    .search_dropdown img {
        width: 80px;
        height: 80px;
    }

    .search_container ul {
        display: flex;
        justify-content: left;
        flex-direction: column;
        gap: 15px;
        overflow-x: auto;
        transform: none;
        position: unset;
        padding: 0 15px;
    }

}

